@import url(https://fonts.googleapis.com/css?family=Charm:400,700);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
* {
  padding: 0;
  margin: 0;
  font-family: 'Charm', sans-serif; }

#wrapper {
  background: url("http://api.thumbr.it/whitenoise-361x370.png?background=4ea6ca00&noise=626262&density=52&opacity=30");
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.myLink {
  margin-top: 30px; }
  .myLink a {
    color: #ffffff;
    font-weight: 600; }

#quote-box {
  background: aliceblue;
  width: 40%;
  padding: 40px 50px;
  display: flex;
  flex-direction: column;
  border-radius: 3px;
  box-shadow: 0px 3px 113px -7px rgba(0, 0, 0, 0.93); }
  #quote-box .quote-text {
    color: #8a8686; }
    #quote-box .quote-text .fa-quote-left {
      font-size: 35px; }
    #quote-box .quote-text #text {
      font-size: 35px;
      margin-left: 10px; }
  #quote-box .quote-author {
    color: #8a8686;
    align-self: flex-end;
    margin-top: 30px; }
  #quote-box .buttons {
    display: flex;
    margin-top: 80px; }
    #quote-box .buttons .button {
      font-family: 'Raleway', sans-serif;
      display: inline-block;
      border: none;
      color: #ffffff;
      -webkit-transition: 0.4s;
      transition: 0.4s;
      cursor: pointer; }
      #quote-box .buttons .button#tweet-quote {
        font-size: 35px; }
      #quote-box .buttons .button#tumblr-quote {
        margin-left: 30px;
        font-size: 35px; }
      #quote-box .buttons .button#new-quote {
        background: #ffffff;
        margin-left: auto;
        padding: 16px 32px;
        font-size: 16px;
        border: 2px solid; }

